<template>
  <div>
    <div v-if="pageLoading" class="text-center">
      <i class="fa fa-spinner fa-spin fa-2x" />
    </div>
    <div v-else>
      <b-row>
        <b-button @click="goToBack" variant="primary" class="mb-3 ml-2">{{
          $t("BACK")
        }}</b-button>
      </b-row>
      <div class="title-fw title-fs-max m-left-3">
        {{ $t("SUBSIDIESTEXT") }}
      </div>
      <br />
      <b-row>
        <b-col xl="1"></b-col>
        <b-col xl="2" class="title-fw title-fs-2">{{
          $t("COMPANYNAMEORNAME")
        }}</b-col>
        <b-col xl="7" class="title-fs-2">{{
          clientAccountData.client_name ? clientAccountData.client_name : "-"
        }}</b-col>
      </b-row>
      <br />
      <b-row>
        <b-col xl="1"></b-col>
        <b-col xl="2" class="title-fw title-fs-2">{{ $t("INDUSTRY") }}</b-col>
        <b-col xl="7" class="title-fs-2">{{
          clientAccountData.industry_nm ? clientAccountData.industry_nm : "-"
        }}</b-col>
      </b-row>
      <br />
      <b-row>
        <b-col xl="1"></b-col>
        <b-col xl="2" class="title-fw title-fs-2">{{
          $t("PREFECTURES")
        }}</b-col>
        <b-col xl="7" class="title-fs-2">{{
          clientAccountData.value_string_small ? clientAccountData.value_string_small : "-"
        }}</b-col>
      </b-row>
      <br />
      <b-row>
        <b-col xl="1"></b-col>
        <b-col xl="2" class="title-fw title-fs-2">{{
          $t("SUBSIDYGRANTJUDGEMENTRESULT")
        }}</b-col>
        <b-col xl="7" class="title-fs-2">{{
          judgResultData.judge_result
        }}</b-col>
      </b-row>
      <br /><br /><br />
      <b-row class="d-flex align-items-center">
        <b-col xl="2"></b-col>
        <b-col xl="6" class="title-fw">{{ $t("SENDMEMO") }}</b-col>
        <b-col xl="2">
          <b-row class="d-flex align-items-center mb-1 no-gutters">
            <label
              class="highLightHeaderLabel link-color mt-1"
              >{{ $t("CONSENT") }}</label
            >
            <b-checkbox v-model="agree" class="ml-1"></b-checkbox>
          </b-row>
        </b-col>
        <b-col>
          <b-button
            @click="goToSubsidyAppComplete"
            :disabled="!agree || loading"
            variant="primary"
            >{{ $t("SEND") }} <i v-if="loading" class="fa fa-spinner fa-spin"
          /></b-button>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { getDateHourMinute } from "@/shared/utils";
import { mapGetters } from "vuex";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "JudgementResult",
  components: {},
  data() {
    return {
      createdAssessmentNo: "",
      pageLoading: false,
      loading: false,
      agree: false,
      clientAccountData: {},
      judgResultData: {},
    };
  },
  computed: {
    ...mapGetters({
      resultData: "getSessionJudgementResult",
      subsidyQuestionAnswers: "getSessionQA",
      client_details: "getUserDetails",
    }),
  },
  mounted() {
    this.getConfirmationResult();
  },
  methods: {
    /**Based on result number display result summary and display client details */
    async getConfirmationResult() {
      this.pageLoading = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTCLIENTCOMPANYDETAILS,
        {
          client_no: localStorage.getItem("client_account_no"),
          result_no: this.resultData.result_no,
        }
      );
      this.pageLoading = false;
      if (data.success) {
        if(data.data.clientCompanyDetails.length > 0){
          this.clientAccountData = data.data.clientCompanyDetails[0];
        }
        else{
          this.clientAccountData = {
            client_name : "",
            industry_nm : "",
            value_string_small : ""
          }
        }
        this.judgResultData = data.data.ClientResultDetails;
      }
    },

    /**Function to go back to the judgement result screen */
    goToBack() {
      this.$router.push({ name: "subsidyGrantJudgement_result" });
    },

    /**Function to create the successful html popup message */
    getSuccessHtml() {
      let successHtml =
        `<div class="title-fw title-fs-max text-left">` +
        this.$t("ASSESSMENTREQUESTTEXT.LINE1") +
        `<br />` +
        this.$t("ASSESSMENTREQUESTTEXT.LINE2") +
        `<br />
        <br />
        ` +
        "労務関連補助金：査定依頼No[" +
        this.createdAssessmentNo +
        "]" +
        `<br />
        <br />` +
        this.$t("ASSESSMENTREQUESINQUARY") +
        `
      </div>`;
      return successHtml;
    },

    /**Function to send subsidy data to DB table and mentioned to send mail to client regarding the subsidy Task */
    async goToSubsidyAppComplete() {
      this.loading = true;
      let questionAnswerDataArray = [];
      let selectedQA = this.subsidyQuestionAnswers;
      if (selectedQA.length > 0) {
        let subsidyType = selectedQA[0].question.subsidy_type;
        selectedQA.forEach((item) => {
          let questionAnswer = {
            question_no: item.question.question_no,
            answer_no: item.answer.answerNo,
          };
          questionAnswerDataArray.push(questionAnswer);
        });
        let assessmentObjectData = {
          client_no: this.client_details.client_account_no,
          client_person_name: this.client_details.clientPersonName,
          client_name: this.client_details.clientCompanyName,
          icon_path: this.client_details.icon_path,
          subsidy_type: subsidyType,
          judge_result_kbn: this.judgResultData.judge_result_kbn,
          assessment_sts_kbn: "01",
          assessment_dt: getDateHourMinute(new Date()),
          request_date: getDateHourMinute(new Date()),
          create_id: localStorage.getItem("client_account_no"),
          update_id: localStorage.getItem("client_account_no"),
        };
        let payload = {
          questionAnswerData: questionAnswerDataArray,
          assessmentData: assessmentObjectData,
        };
        const { data } = await BasicInfoApis.postAPI(
          endPoint.SUBSIDYCONFIRMATIONSEND,
          payload
        );
        this.loading = false;
        if (data.success) {
          this.createdAssessmentNo = data.data.assessment_no;
          let successHtml = this.getSuccessHtml();
          this.$swal({
            width: 700,
            type: "success",
            html: successHtml,
            customClass: {
              popup: "format-pre",
            },
          }).then(() => {
            this.$router.push({ name: "subsidy_grant_judgement_index" });
          });
        }
      }
    },
  },
};
</script>

<style>
.title-fs-max {
  font-size: 25px;
}
.text-left {
  text-align: left !important;
}
.highLightHeaderLabel {
  font-weight: 700 !important;
  padding-top: 5px;
}
</style>
